import React from 'react';
import Carousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';
import a from '../images/profile.jpg';

class Gallery extends React.Component {
  constructor() {
    super();
    this.state = { value: 0 };
    this.onChange = this.onChange.bind(this);
  }

  onChange(value) {
    this.setState({ value });
  }

  render() {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          flexFlow: 'row',
          alignContent: 'center',
          textAlign: 'center',
        }}
      >
        {' '}
        <Carousel
          centered
          clickToChange
          slidesPerPage={3}
          value={this.state.value}
          onChange={this.onChange}
          slides={[<img src={a} />, <img src={a} />, <img src={a} />]}
          arrows
          clickToChange
        />
        <span class="caption">My fav Blogs/Podcasts</span>
      </div>
    );
  }
}

export default Gallery;



